body {
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  margin: 0;
  padding: 0;
  min/* Additional Styles */
ul {
  padding-left: 20px;
}

.error {
  color: #e53e3e;
  font-weight: 500;
  background: #fed7d7;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #feb2b2;
}
.success {
  color: #38a169;
  font-weight: 500;
  background: #c6f6d5;
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #9ae6b4;
}

button.small { 
  padding: 6px 12px; 
  font-size: 0.8rem;
  background: linear-gradient(135deg, #4299e1, #3182ce);
}

.top-actions { 
  display: flex; 
  justify-content: space-between; 
  align-items: center; 
  margin-bottom: 20px;
}

/* Modal Styles */
.modal-backdrop { 
  position: fixed; 
  inset: 0; 
  background: rgba(0,0,0,0.6); 
  display: flex; 
  align-items: center; 
  justify-content: center;
  backdrop-filter: blur(4px);
  z-index: 1000;
  animation: modalFadeIn 0.2s ease-out;
}
.modal { 
  background: #fff; 
  padding: 0; 
  border-radius: 12px; 
  width: 90%; 
  max-width: 500px;
  box-shadow: 0 20px 60px rgba(0,0,0,0.3);
  animation: modalSlideIn 0.3s ease-out;
}
.modal-header { 
  display: flex; 
  justify-content: space-between; 
  align-items: center;
  padding: 20px 24px;
  border-bottom: 1px solid #e2e8f0;
}
.modal-header h4 {
  margin: 0;
  color: #1a202c;
}
.modal-header button {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #718096;
  padding: 4px;
  border-radius: 4px;
}
.modal-header button:hover {
  background: #f7fafc;
}
.modal-body { 
  padding: 24px;
}

@keyframes modalFadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes modalSlideIn {
  from { transform: scale(0.9) translateY(-20px); opacity: 0; }
  to { transform: scale(1) translateY(0); opacity: 1; }
}

/* Table Styles */
.agents-table { 
  width: 100%; 
  border-collapse: collapse; 
  margin-top: 20px;
}
.agents-table th, .agents-table td { 
  text-align: left; 
  padding: 12px 16px; 
  border-bottom: 1px solid #e2e8f0;
}
.agents-table th {
  background: #f8fafc;
  font-weight: 600;
  color: #4a5568;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.agents-table tbody tr:hover {
  background: #f8fafc;
}}

.app-container {
  max-width: 1200px;
  margin: 20px auto;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0,0,0,0.1);
  padding: 32px;
  backdrop-filter: blur(10px);
  animation: fadeIn 0.5s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.site-header {
  display: flex;
  align-items: center;
  gap: 16px;
  justify-content: space-between;
  padding: 16px 32px;
  background: rgba(255,255,255,0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0,0,0,0.1);
  margin-bottom: 20px;
  position: sticky;
  top: 0;
  z-index: 100;
}
.brand { 
  font-weight: 700; 
  color: #667eea;
  font-size: 1.2rem;
  background: linear-gradient(135deg, #667eea, #764ba2);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.site-nav {
  display: flex;
  gap: 24px;
}
.site-nav a { 
  color: #374151;
  text-decoration: none;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  transition: all 0.2s;
}
.site-nav a:hover {
  background: rgba(102, 126, 234, 0.1);
  color: #667eea;
}


/* Modern UI Components */
.card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
  margin-bottom: 24px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  position: relative;
}
.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 30px rgba(0,0,0,0.12);
}
.loading-bar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #007bff, #0056b3);
  animation: loading 2s infinite;
}
@keyframes loading {
  0%, 100% { transform: translateX(-100%); }
  50% { transform: translateX(100%); }
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 24px 0;
}
.card-body {
  padding: 20px 24px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 32px;
}
.stat-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 24px;
  border-radius: 12px;
  text-align: center;
}
.stat-value {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 8px;
}
.stat-label {
  opacity: 0.9;
  font-size: 0.9rem;
}
.stat-change {
  margin-top: 8px;
  font-size: 0.8rem;
}
.stat-change.positive { color: #4ade80; }
.stat-change.negative { color: #f87171; }

.search-box {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
}
.search-input {
  padding: 12px 40px 12px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  width: 300px;
  transition: border-color 0.2s;
}
.search-input:focus {
  border-color: #007bff;
  outline: none;
}
.search-icon {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
}

.pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 24px;
}
.pagination-btn {
  padding: 8px 12px;
  border: 1px solid #e2e8f0;
  background: white;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}
.pagination-btn:hover:not(:disabled) {
  background: #f8fafc;
}
.pagination-btn.active {
  background: #007bff;
  color: white;
  border-color: #007bff;
}
.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

h2, h3 {
  color: #333;
  margin-top: 0;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

input, select, button {
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 1rem;
}

button {
  background: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}
button:hover {
  background: #0056b3;
}
button.small { padding:6px 8px; font-size:0.9rem }

.agents-table { width:100%; border-collapse: collapse; }
.agents-table th, .agents-table td { text-align:left; padding:8px; border-bottom:1px solid #eee }
.top-actions { display:flex; justify-content:space-between; align-items:center; margin-bottom:12px }

nav {
  margin-bottom: 24px;
}
nav a {
  color: #007bff;
  text-decoration: none;
  margin-right: 16px;
  font-weight: 500;
}
nav a:hover {
  text-decoration: underline;
}

ul {
  padding-left: 20px;
}

.error {
  color: #d32f2f;
  font-weight: 500;
}
.success {
  color: #388e3c;
  font-weight: 500;
}
